
@font-face {
  font-family: fangsong;
  src: url("../assets/WenYueGuTiFangSong-min.ttf") format("truetype");
}
html,
body {
  height: 100%;
  margin: 0;
  overflow: hidden;
}
#app {
  height: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: "";
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
#scroll {
  height: 100%;
  width: 100%;
  background-size: cover;
  background-position: right;
}
#cover {
  height: 100%;
  width: 100%;
}
#coverhover {
  margin-left: 100px;
  height: 100%;
  width: 100%;
  visibility: hidden;
  overflow: hidden;
  background-image: -webkit-gradient(
    linear,
    left top, right top,
    from(rgba(255, 255, 255, 0)),
    color-stop(50%, rgba(0, 0, 0, 0.26)),
    color-stop(65%, rgba(0, 0, 0, 0.73)),
    color-stop(80%, rgba(0, 0, 0, 0.9251050762101716))
  ) !important;
  background-image: linear-gradient(
    90deg,
    rgba(255, 255, 255, 0) 0%,
    rgba(0, 0, 0, 0.26) 50%,
    rgba(0, 0, 0, 0.73) 65%,
    rgba(0, 0, 0, 0.9251050762101716) 80%
  ) !important;
  opacity: 0;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
#coverhover:hover {
  height: 100%;
  color: white;
  width: 100%;
  opacity: 1;
  /* background-image: linear-gradient(90deg, rgba(255,255,255,0) 0%, rgba(38,15,0,0.26) 45%, rgba(38,15,0,0.73) 57%, rgba(38,15,0,0.9251050762101716) 70%)!important; */
}
p {
  margin: 10px;
}
#title {
  padding-top: 50px;
  padding-left: 60%;
  padding-right: 4%;
  font-family: fangsong;
  font-size: 80px;
  height: 100%;
  opacity: 0;
  overflow: hidden;
  visibility: hidden;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
#title:hover {
  opacity: 1;
}
#en_title {
  font-size: 45px;
}
#desc {
  margin-right: 20%;
  margin-top: 6%;
  font-size: 22px;
  text-align: justify;
  text-justify: inter-ideograph;
  text-indent: 2em;
}
.button {
  margin-left: 2%;
  border-radius: 0;
  padding: 10px 25px 10px 25px;
  font-size: 22px;
  color: #ffffff;
  border: solid #ffffff 1px;
  text-decoration: none;
  display: inline-block;
  text-align: center;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
.button:hover {
  background: #ffffff;
  color: rgba(0, 0, 0, 1);
  border-radius: 0;
  text-decoration: none;
}
hr {
  border-color: rgb(165, 165, 165);
}
#footer {
  margin-left: 1%;
  font-size: 18px;
  text-align: justify;
  text-justify: inter-ideograph;
  position: absolute;
  color: rgb(165, 165, 165);
  bottom: 20px;
}



